<template>
  <div class="sample-list" v-loading="loading">
    <div class="filter">
      <div class="item" @click="switchStatus(0)" :class="{ 'item-selected': status === 0 }">待处理</div>
      <div class="item" @click="switchStatus(1)" :class="{ 'item-selected': status === 1 }">已处理</div>
    </div>
    <div class="panel">
      <div class="talent">达人信息</div>
      <div class="shop">商品信息</div>
      <div class="address">收获信息</div>
      <div class="operation">操作</div>
    </div>
    <div class="panel" v-for="(item, index) in dataArray" :key="index">
      <div class="talent">
        <div class="item">微信号：{{item.wechatAccount}}</div>
        <div class="item">达人名：{{item.memberUsername}}</div>
      </div>
      <div class="shop">
        <img :src="tk_logo">
        <div class="info">
          <div class="item">{{item.productName}}</div>
          <div class="item">商品ID：{{item.productId}}</div>
          <div class="item">店铺：{{item.shopName}}</div>
        </div>
      </div>
      <div class="address">
        <div class="item">收货人：{{item.receiverName}}</div>
        <div class="item">手机号：{{item.receiverPhone}}</div>
        <div class="item">地址：{{item.receiverDetailAddress}}</div>
      </div>
      <div class="operation">
        <div class="reject" @click="rejectSample(item)">拒绝寄样</div>
        <div class="resolve" @click="resolveSample(item)">同意寄样</div>
      </div>
    </div>
    <el-pagination
      background
      layout="->, prev, pager, next"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
import { getchOrderList, updateDeliveryOrder } from '@/api/tkpro'
import tk_logo from '@/assets/images/tk_logo.jpg';
export default {
  name: 'sample-list',
  data() {
    return {
      tk_logo,
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      status: 0,  // 审核状态：0->未审核；1->审核通过
      dataArray: [],
    }
  },
  created(){
    this.loadData()
  },
  methods:{
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.loadData()
      console.log(`当前页: ${val}`);
    },
    loadData () {
      this.loading = true
      getchOrderList(this.status, this.currentPage).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.dataArray = res.data.list
        this.total = res.data.total
        // this.pageSize = res.data.pageSize
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    },
    switchStatus (index) {
      if (this.status === index) return
      this.status = index
      this.loadData()
    },
    rejectSample () {
      const h = this.$createElement;
      this.$msgbox({
        title: '请输入拒绝理由：',
        distinguishCancelAndClose: false,
        closeOnClickModal: false,
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        message: h('p', null, [
          h('textarea', {}, '')
        ]),
      }).then(({ value }) => {  // 订单更新状态，1->同意寄样；2->拒绝寄样
        const input = {
        	"deliveryCompany": "圆通快递",
          "deliverySn": "yt8888888888888",
          "denyReason": value,
          "orderSn": "8888888888",
          "orderStatus": 2
        }
        this.loading = true
        updateDeliveryOrder(input).then(res => {
          if (res.code !== 200) return this.$message.error(res.message);
          this.loading = false
          this.loadData()
          this.$message({ type: 'success', message: '操作成功' });
        }).catch(error => {
          console.log(error);
          this.loading = false
        })
      }).catch(() => {
      });
    },
    resolveSample () {
      const h = this.$createElement;
      this.$msgbox({
        title: '请输入物流单号：',
        distinguishCancelAndClose: false,
        closeOnClickModal: false,
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        message: h('p', null, [
          h('textarea', {}, '')
        ]),
      }).then(({ value }) => {  // 订单更新状态，1->同意寄样；2->拒绝寄样
        const input = {
        	"deliveryCompany": "圆通快递",
          "deliverySn": "yt8888888888888",
          "denyReason": value,
          "orderSn": "8888888888",
          "orderStatus": 1
        }
        this.loading = true
        updateDeliveryOrder(input).then(res => {
          if (res.code !== 200) return this.$message.error(res.message);
          this.loading = false
          this.loadData()
          this.$message({ type: 'success', message: '操作成功' });
        }).catch(error => {
          console.log(error);
          this.loading = false
        })
      }).catch(() => {
      });
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.sample-list {
  background: #eeeeee;
  padding: 20px;
  .filter {
    display: flex;
    margin-bottom: 17px;
    .item {
      margin-right: 58px;
      color: rgba(3, 3, 3, 100);
      font-size: 18px;
      cursor: pointer;
    }
    .item-selected {
      color: #6FB2DF;
      font-weight: bold;
    }
  }
  .panel {
    line-height: 20px;
    display: flex;
    background-color: rgba(255, 255, 255, 100);
    text-align: center;
    border: 1px solid rgba(187, 187, 187, 100);
    padding: 10px 0 10px 65px;
    margin-bottom: 20px;
    .talent {
      color: rgba(16, 16, 16, 100);
      font-size: 14px;
      width: 18%;
      display: flex;
      flex-direction: column;
      text-align: left;
      .item {
        color: rgba(16, 16, 16, 100);
        font-size: 14px;
      }
    }
    .shop {
      width: 39%;
      display: flex;
      text-align: left;
      img {
        width: 70px;
        height: 70px;
        margin-right: 5px;
      }
      .info {
        display: flex;
        flex-direction: column;
        .item {
          color: rgba(16, 16, 16, 100);
          font-size: 14px;
        }
      }
    }
    .address {
      width: 16%;
      display: flex;
      flex-direction: column;
      text-align: left;
      .item {
        color: rgba(16, 16, 16, 100);
        font-size: 14px;
      }
    }
    .operation {
      width: 22%;
      display: flex;
      flex-direction: row;
      align-items: center;
      text-align: left;
      .reject {
        width: 80px;
        height: 30px;
        border-radius: 4px;
        background-color: rgba(111, 178, 223, 100);
        color: rgba(255, 255, 255, 100);
        font-size: 14px;
        line-height: 28px;
        margin-right: 37px;
        text-align: center;
        cursor: pointer;
      }
      .resolve {
        width: 80px;
        height: 30px;
        border-radius: 4px;
        background-color: rgba(164, 173, 179, 100);
        color: rgba(255, 255, 255, 100);
        font-size: 14px;
        line-height: 28px;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}

textarea {
  width: 100%;
  height: 120px;
  line-height: 20px;
  color: rgba(136, 136, 136, 100);
  font-size: 14px;
  text-align: left;
  border: 1px solid rgba(187, 187, 187, 100)
}
</style>



